.credit-card-icon {
	font-size: 25px;
}

.credit-card-background {
	height: 40px;
	width: 40px;
}

.info-icon {
	font-size: 20px;
}

.material-symbols-outlined {
	font-variation-settings: 'FILL' 0;
}

.mail-icon {
	font-size: 15px;
	width: 15px;
	display: inline-block !important;
	height: 15px;
}

.cancel-alert {
	border: 1px solid var(--error-2);
	border-radius: 4px;
}

.active-plan {
	border: 1px solid var(--success-2);
}

.cancelled-plan {
	border: 1px solid var(--error-2);
}

.subscription-badge {
	height: 25px;
	border-radius: 4px;
	line-height: 22px;
}

.price-page-link {
	height: 40px;
	border-radius: 4px;
	width: fit-content;

	.mat-icon {
		width: 18px;
		height: 18px;
	}
}

.mat-icon.d-inline-block {
	display: inline-block !important;
}

.border-neutral-1 {
	border: 1px solid var(--neutral-1) !important;
}

.h-26px {
	height: 26px !important;
}
